.heading {
    font-size: 30px;
    color: blue;
}

.dialog__content {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-column-gap: 50px;
}

.chatemate_person {
    color: darkblue;
    font-size: 20px;
    cursor: pointer;
}

.chatemate_person .active {
    font-size: 25px;
}

.converation {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.message {
    font-size: 20px;
    color: black;
}